.home-container {
    background: linear-gradient(135deg, rgba(27,27,47,1) 7%, rgba(31,64,104,1) 48%, rgba(228,63,90,1) 100%);
    color: white;


    .home-header {
        margin: 20px 0 0;
    }

    .home-body-container {
        @include flexCenter(column, center);
        text-align: center;

        h3 {
            margin: 10px 0;
        }

        h4 {
            margin: 0 0 20px;
        }
        
        a {
            color: white;
            text-decoration: none;
        }
    }

    .composers-home-page {
        @include homePageGrid();


        .composer-card-home-page {
            display: grid;
            grid-template-columns: 100%;
            grid-template-rows: 200px 50px;
            grid-template-areas: 
                "img"
                "p";
                img {
                    object-fit: cover;
                    height: 100%;
                    width: 100%;
                    grid-area: img;
                }

                p {
                    font-size: 14px;
                    font-weight: lighter;
                    letter-spacing: .4px;
                    margin: 0;
                    grid-area: p;
                    background: rgba(0, 0, 0, 0.534);
                    @include flexCenter(column, center)
                }
        }
    }

   

   
}
