$primaryColor: #162447;
$secondaryDarkColor: #1b1b2f;
$secondaryLightColor: #1f4068;
$accentColor: #e43f5a;

@mixin flexCenter($direction, $justify) {
    display: flex;
    justify-content: $justify;
    align-items: center;    
    flex-direction: $direction;
}

@mixin homePageGrid(){
    display: grid;
        grid-template-columns: 50% 50%;
        gap: 0px 0px;
        grid-template-areas:
          ". ."
          ". .";
        gap: 5px 5px;
        width: 90%;
}

@import './header'; 
@import './home'; 
@import './genresHomePage';
@import './footer';
@import './erasHomePage';
@import './composerPage';
@import './wikiPage';

@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500;700&display=swap');

body, * {
    margin: 0;
    font-family: 'Ubuntu', sans-serif;
}


#home-page-section-title {
    margin: 30px;
    font-size: 18px;
}